body {
  font-family: "Nunito Sans", sans-serif !important;
  background-color: #f5f5f5;
}
.card {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.12);
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
}
.card td {
  vertical-align: middle;
}

.btn:focus,
.btn:visited {
  box-shadow: none !important;
}
